idth: -moz-fit-content;\n  width: fit-content;\n}\n\n.cart-total {\n  padding: 0;\n}\n.cart-total li {\n  margin: 3rem 0;\n}\n.cart-total li:first-child {\n  margin-left: 3rem;\n}\n.cart-total li:last-child {\n  height: 100%;\n  min-width: 14rem;\n  color: white;\n  padding: 0 3rem;\n  background: var(--brand-base-color);\n  -webkit-clip-path: var(--clip-path);\n          clip-path: var(--clip-path);\n}\n\n/*=================\n  Aside: Checkout\n===================*/\n.cart__staging--place-order {\n  gap: 6rem;\n  width: 35rem;\n  height: 100%;\n  top: 0;\n  position: -webkit-sticky;\n  position: sticky;\n  background: var(--background);\n  padding: 3rem 1rem 1rem;\n  display: flex;\n  flex-direction: column-reverse;\n  justify-content: space-around;\n}\n@media (max-width: 1000px) {\n  .cart__staging--place-order {\n    position: relative;\n    gap: 2rem;\n    align-items: flex-end;\n    width: 100%;\n  }\n}\n@media (max-width: 1000px) {\n  .cart__staging--place-order h3 {\n    top: 1rem;\n    left: 2rem;\n    position: absolute;\n  }\n}\n\n.checkout-button {\n  color: black;\n  font-size: 2.5rem;\n  background-color: rgb(140, 200, 140);\n  width: 100%;\n  height: 6rem;\n  border-radius: 1rem;\n  border: 0.2rem solid gray;\n  transition: 0.15s;\n}\n@media (max-width: 1000px) {\n  .checkout-button {\n    width: 25%;\n  }\n}\n\n.checkout-button:hover {\n  color: white;\n  background-color: lightseagreen;\n}\n\n.payment-list {\n  display: grid;\n  border-radius: 1rem;\n  grid-template-columns: 1fr 1fr;\n  box-shadow: 0 0 0.5rem 0.1rem rgb(177, 177, 177);\n}\n.payment-list li {\n  margin: 1rem;\n  display: flex;\n  justify-content: center;\n}\n@media (max-width: 1000px) {\n  .payment-list {\n    grid-template-columns: repeat(6, 1fr);\n  }\n}\n\n.pay-logo {\n  width: 75%;\n  height: auto;\n}\n\n.pay-logo-bg {\n  fill: lightgrey;\n}/*# sourceMappingURL=Cart.style.css.map */", "",{"version":3,"sources":["webpack://./src/styles/Cart.style.scss","webpack://./src/styles/Cart.